	module.exports = {"data":{"postsJson":{"title":"갈리리에서의 선교를 완수하다","questionsTitle":"과거로 부터 배움.","display":"block","bookName":"누가복음","ref":"9:1-9:50","html":"<p>   <strong>9</strong><u>1</u>예수께서 열두 제자를 불러 모으사 모든 귀신을 제어하며 병을 고치는 능력과 권위를 주시고 <u>2</u>하나님의 나라를 전파하며 앓는 자를 고치게 하려고 내보내시며 <u>3</u>이르시되</p><h1>예수님</h1><h2>여행을 위하여 아무 것도 가지지 말라 지팡이나 배낭이나 양식이나 돈이나 두 벌 옷을 가지지 말며 <u>4</u>어느 집에 들어가든지 거기서 머물다가 거기서 떠나라 <u>5</u>누구든지 너희를 영접하지 아니하거든 그 성에서 떠날 때에 너희 발에서 먼지를 떨어 버려 그들에게 증거를 삼으라</h2><p>하시니 <u>6</u>제자들이 나가 각 마을에 두루 다니며 곳곳에 복음을 전하며 병을 고치더라<br />   <u>7</u>분봉 왕 헤롯이 이 모든 일을 듣고 심히 당황하니 이는 어떤 사람은 요한이 죽은 자 가운데서 살아났다고도 하며 <u>8</u>어떤 사람은 엘리야가 나타났다고도 하며 어떤 사람은 옛선지자 한 사람이 다시 살아났다고도 함이라<br />   <u>9</u>헤롯이 이르되</p><h1>헤롯왕</h1><h4>요한은 내가 목을 베었거늘 이제 이런 일이 들리니 이 사람이 누군가</h4><p>하며 그를 보고자 하더라<br />   <u>10</u>사도들이 돌아와 자기들이 행한 모든 것을 예수께 여쭈니 데리시고 따로 벳새다라는 고을로 떠나 가셨으나 <u>11</u>무리가 알고 따라왔거늘 예수께서 그들을 영접하사 하나님 나라의 일을 이야기하시며 병 고칠 자들은 고치시더라<br />   <u>12</u>날이 저물어 가매 열두 사도가 나아와 여짜오되</p><h1>제자들</h1><h3>무리를 보내어 두루 마을과 촌으로가서 유하며 먹을 것을 얻게 하소서 우리가 있는 여기는 빈 들이니이다</h3><p><u>13</u>예수께서 이르시되</p><h1>예수님</h1><h2>너희가 먹을 것을 주라</h2><p>하시니 여짜오되</p><h1>제자들</h1><h3>우리에게 떡 다섯개와 물고기 두 마리밖에 없으니 이 모든 사람을 위하여 먹을 것을 사지 아니하고서는 할 수 없사옵나이다</h3><p>하니 <u>14</u>이는 남자가 한 오천 명 됨이러라<br />   제자들에게 이르시되</p><h1>예수님</h1><h2>떼를 지어 한 오십 명씩 앉히라</h2><p>하시니 <u>15</u>제자들이 이렇게 하여 다 앉힌 후 <u>16</u>예수께서 떡 다섯 개와 물고기 두 마리를 가지사 하늘을 우러러 축사하시고 떼어 제자들에게 주어 무리에게 나누어 주게 하시니 <u>17</u>먹고 다 배불렀더라 그 남은 조각을 열두 바구니에 거두니라<br />   <u>18</u>예수께서 따로 기도하실 때에 제자들이 주와 함께 있더니 물어 이르시되</p><h1>예수님</h1><h2>무리가 나를 누구라고 하느냐</h2><p><u>19</u>대답하여 이르되</p><h1>제자들</h1><h3>세례 요한이라 하고 더러는 엘리야라, 더러는 옛 선지자 중의 한 사람이 살아났다 하나이다</h3> <p><u>20</u>예수께서 이르시되</p><h1>예수님</h1><h2>너희는 나를 누구라 하느냐</h2><p>베드로가 대답하여 이르되</p><h1>시몬 베드로</h1><h3>하나님의 그리스도시니이다</h3><p>하니 <u>21</u>경고하사</p><h1>예수님</h1><h2>이 말을 아무에게도 이르지 말라</h2><p>명하시고 <u>22</u>이르시되</p><h1>예수님</h1><h2>인자가 많은 고난을 받고 장로들과 대제사장들과 서기관들에게 버린 바 되어 죽임을 당하고 제삼일에 살아나야 하리라</h2><p>하시고 <u>23</u>또 무리에게 이르시되</p><h1>예수님</h1><h2>아무든지 나를 따라오려거든 자기를 부인하고 날마다 제 십자가를 지고 나를 따를 것이니라 <u>24</u>누구든지 제 목숨을 구원하고자 하면 잃을 것이요 누구든지 나를 위하여 제 목숨을 잃으면 구원하리라 <u>25</u>사람이 만일 온 천하를 얻고도 자기를 잃든지 빼앗기든지 하면 무엇이 유익하리요 <u>26</u>누구든지 나와 내 말을 부끄러워하면 인자도 자기와 아버지와 거룩한 천사들의 영광으로 올 때에 그 사람을 부끄러워하리라 <u>27</u>내가 참으로 너희에게 이르노니 여기 서 있는 사람 중에 죽기 전에 하나님의 나라를 볼 자들도 있느니라</h2><p><u>28</u>이 말씀을 하신 후 팔 일쯤 되어 예수께서 베드로와 요한과 야고보를 데리고 기도하시러 산에 올라가사 <u>29</u>기도하실 때에 용모가 변화되고 그 옷이 희어져 광채가 나더라 <u>30</u>문득 두 사람이 예수와 함께 말하니 이는 모세와 엘리야라 <u>31</u>영광중에 나타나서 장차 예수께서 예루살렘에서 별세하실 것을 말할새<u>32</u>베드로와 및 함께 있는 자들이 깊이 졸다가 온전히 깨어나 예수의 영광과 및 함께 선 두 사람을 보더니 <u>33</u>두 사람이 떠날 때에 베드로가 예수께 여짜오되</p><h1>시몬 베드로</h1><h3>주여 우리가 여기 있는 것이 좋사오니 우리가 초막 셋을 짓되 하나는 주를위하여, 하나는 모세를 위하여,하나는 엘리야를 위하여 하사이다</h3><p>하되 자기가 하는 말을 자기도 알지 못하더라<br />   <u>34</u>이 말 할 즈음에 구름이 와서 그들을 덮는지라 구름 속으로 들어갈 때에 그들이 무서워하더니 <u>35</u>구름 속에서 소리가 나서 이르되</p><h1>하나님</h1><h2>이는 나의 아들 곧 택함을 받은 자니 너희는 그의 말을 들으라</h2><p>하고 <u>36</u>소리가 그치매 오직 예수만 보이더라 제자들이 잠잠하여 그 본 것을 무엇이든지 그 때에는 아무에게도 이르지 아니하니라<br />   <u>37</u>이튿날 산에서 내려오시니 큰 무리가 맞을새 <u>38</u>무리 중의 한 사람이 소리 질러 이르되</p><h1>무리중 한 사람</h1><h4>선생님 청컨대 내 아들을 돌보아 주옵소서 이는 내 외아들이니이다 <u>39</u>귀신이 그를 잡아 갑자기 부르짖게 하고 경련을 일으켜 거품을 흘리게 하며 몹시 상하게 하고야 겨우 떠나 가나이다 <u>40</u>당신의 제자들에게 내쫓아 주기를 구하였으나 그들이 능히 못하더이다</h4><p><u>41</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>믿음이 없고 패역한 세대여 내가 얼마나 너희와 함께 있으며 너희에게 참으리요 네 아들을 이리로 데리고 오라</h2><p>하시니 <u>42</u>올 때에 귀신이 그를 거꾸러뜨리고 심한 경련을 일으키게 하는지라 예수께서 더러운 귀신을 꾸짖으시고 아이를 낫게 하사 그 아버지에게 도로 주시니 <u>43</u>사람들이 다 하나님의 위엄에 놀라니라<br />   그들이 다 그 행하시는 모든 일을 놀랍게 여길새 예수께서 제자들에게 이르시되</p><h1>예수님</h1><h2>44이 말을 너희 귀에 담아 두라 인자가 장차 사람들의 손에 넘겨지리라</h2><p>하시되 <u>45</u>그들이 이 말씀을 알지 못하니 이는 그들로 깨닫지 못하게 숨긴 바 되었음이라 또 그들은 이 말씀을 묻기도 두려워하더라<br />   <u>46</u>제자 중에서 누가 크냐 하는 변론이 일어나니 <u>47</u>예수께서 그 마음에 변론하는 것을 아시고 어린 아이 하나를 데려다가 자기 곁에 세우시고 <u>48</u>그들에게 이르시되</p><h1>예수님</h1><h2>누구든지 내 이름으로 이런 어린 아이를 영접하면 곧 나를 영접함이요 또 누구든지 나를 영접하면 곧 나를 보내신 이를 영접함이라 너희 모든 사람 중에 가장 작은 그가 큰 자니라</h2><p><u>49</u>요한이 여짜오되</p><h1>제자 요한</h1><h3>주여 어떤 사람이 주의 이름으로 귀신을 내쫓는 것을 우리가 보고 우리와  함께 따르지 아니하므로 금하였나이다</h3><p><u>50</u>예수께서 이르시되</p><h1>예수님</h1><h2>금하지 말라 너희를 반대하지 않는 자는 너희를 위하는 자니라</h2><p>하시니라</p>","path":"/330","Question1":"이 이야기에서 당신이 가장 잘 동일시 할 수 있는 인물은 누구입니까? 왜 입니까?","Question2":"이 구절에있는 이 인물에게서 당신이 보시는 강점들과 혹은 약점들은 무엇입니까?","Question3":"당신의 선택된 인물의 삶에서 하나님이 하시기 원하시는 것은 무엇이라고 생각하십니까?","Question4":"이 구절을 묵상할 때에, 당신의 삶속에서 하나님이 하기 원하시는 것은 무엇이라고 생각하십니까?","prevPath":"/329","nextPath":"/331"}},"pathContext":{}}

